.actions {
    width: 60%;
    min-width: 400px;
    max-width: 600px;
    margin: 0 auto;
    background: #f6f6f6;
    border-radius: 2vw;
    padding: 2vw 4vw;

    form {
        width: 100%;
        height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1vw;

        h2 {
            margin: 0 0 10px 0;
            font-weight: 300;
            line-height: 100%;
            padding: 0;
            width: 100%;
            text-align: center;
        }

        .field {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .firstName,
            .lastName {
                width: 49%;
            }
        }

        fieldset {
            margin: 0;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-style: solid;
            border-color: #eee;
            box-shadow: inset 0 0 1px 0px #eee;
            border-radius: 5px;
            border-width: 1px;
            background: white;

            legend {
                padding: 0 5px;
                color: #888;
                font-weight: 400;
                font-size: clamp(12px, 1vw, 16px);
            }

            input {
                width: 100%;
                outline: none;
                border: none;
                background: transparent;

                &::placeholder {
                    color: #ccc;
                }
            }
        }

        input[type="submit"] {
            width: 100%;
            background: #333;
            color: white;
            margin: 10px 0;
            padding: 10px 0;
            cursor: pointer;
            border-radius: 7px;
        }
    }

    p {
        color: #999;
        width: 100%;
        text-align: center;

        span {
            cursor: pointer;
            color: black;
            text-decoration: underline;
            margin-left: 5px;
            transform: scale(1);
        }
    }

}