.dashboard {
    width: 100vw;
    height: 100vh;
    overflow-y: hidden;
    padding-top: min(10vw, 10vh);
    nav {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        background: white;
        display: flex;
        padding: 1vw 4vw;
        justify-content: space-between;
        align-items: center;

        p {
            font-weight: 300;
            color: #999;

            span {
                color: #333;
                font-weight: 400;
                margin-left: 3px;
            }
        }

        button {
            background: black;
            color: white;
            padding: 5px 20px;
            border-radius: 5px;
            cursor: pointer;
        }
    }

    .authreq,
    .loading {
        width: 100%;
        height: 100%;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        text-align: center;

        h1,
        h2 {
            font-weight: 500;
            width: 100%;
        }

        a {
            padding: 10px 20px;
            border-radius: 5px;
            color: black;
            text-decoration: none;
            background: white;
        }
    }

    main {
        width: 100%;
        padding: 5vw;
        .currRide, .allRides, .bookRide {
            h1 {
                line-height: 100%;
                margin: 0;
                padding: 0;
                margin-bottom: 20px;
                font-weight: 500;
            }
        }
        .bookRide {
            .form {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                input, button {
                    width: 40%;
                    background: #f5f5f5;
                    border-radius: 5px;
                    padding: 10px 20px;
                    outline: none;
                    border: none;
                }
                button {
                    width: 15%;
                    padding: 10px;
                    background: black;
                    color: white;
                }
            }
        }
        .currRide, .bookRide {
            padding: 5vw;
            background: white;
            border-radius: 10px;
            box-shadow: 0 0 1px 1px #aaa;
            margin-bottom: 50px;
        }
        .currRide {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            gap: 20px;
            align-items: center;
            h1 {
                width: 100%;
            }
            p {
                display: flex;
                justify-content: flex-start;
                gap: 10px;
                align-items: center;
                span {
                    text-align: center;
                    color: rgba(80,80,80,1);
                    text-decoration: underline;
                    text-underline-offset: 5px;
                    transform: scale(1.1);
                    padding: 5px 10px;
                    font-weight: 600;
                }
                &.Pickup {
                    span {
                        color: rgba(236, 74, 74, 1);
                    }
                }
                &.Destination {
                    span {
                        color: rgba(44, 135, 255, 1);
                    }
                }
            }
        }
        .allRides {
            margin-top: min(10vw, 8vh);
            width: 100%;
            h1 {
                color: white;
            }
            .head {
                display: flex;
                align-items: center;
                justify-content: space-between;
                button {
                    padding: 10px 30px;
                    background: white;
                    border: none;
                    color: #444;
                    font-weight: 500;
                    border-radius: 30px;
                }
            }
            ul {
                padding: 0;
                list-style: none;
                background: #efefef;
                height: 30vh;
                overflow-y: auto;
                li {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    color: black;
                    padding: 5px 20px;
                    &.header {
                        background: #333;
                        * {
                            color: white !important;
                        }
                    }
                    p {
                        width: 23%;
                        text-align: center;
                        &:first-of-type {
                            color: rgba(236, 74, 74, 1);
                        }
                        &:nth-of-type(2) {
                            color: rgba(44, 135, 255, 1);
                        }
                        &:nth-of-type(3), &:nth-of-type(4) {
                            font-weight: 300;
                        }
                    }
                }
            }
        }
    }

}