.error {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1,
    h2 {
        font-weight: 400;
        color: white;
        font-size: 1rem;
    }

    h1 {
        font-size: 2rem;
    }

    a {
        color: black;
        background: white;
        padding: 10px 20px;
        border-radius: 10px;
        text-decoration: none;
    }

}